/* refer to https://github.com/spoqa/spoqa.github.com/blob/master/css/syntax.css */

.highlight {
	background: $oc-gray-0;
}
.highlight .c {
	color: #999988;
	font-style: italic;
} /* Comment */
.highlight .err {
	color: #a61717;
	background-color: #e3d2d2;
} /* Error */
.highlight .k {
	font-weight: bold;
} /* Keyword */
.highlight .o {
	font-weight: bold;
} /* Operator */
