	color: #999988;
	font-style: italic;
} /* Comment.Multiline */
.highlight .cp {
	color: #999999;
	font-weight: bold;
} /* Comment.Preproc */
.highlight .c1 {
	color: #999988;
	font-style: italic;
} /* Comment.Single */
.highlight .cs {
	color: #999999;
	font-weight: bold;
	font-style: italic;
} /* Comment.Special */
.highlight .gd {
	color: #000000;
	background-color: #ffdddd;
} /* Generic.Deleted */
.highlight .gd .x {
	color: #000000;
	background-color: #ffaaaa;
} /* Generic.Deleted.Specific */
.highlight .ge {
	font-style: italic;
} /* Generic.Emph */
.highlight .gr {
	color: #aa0000;
